.popup__button {
  padding: 0.875rem 0 0.875rem 0;
  background-color: #000000;
  color: #fff;
  border: none;
  cursor: pointer;
  width: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  margin: 3rem 0 0;
  border-radius: 2px;
}

.popup__button:not(:disabled):hover {
  background: #000000;
  opacity: 0.8;
}

@media screen and (max-width: 540px) {
  .popup__button {
    font-size: 14px;
    line-height: 17px;
  }
}
