.profile__name {
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #ffffff;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 20.8rem;
}

@media screen and (max-width: 420px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    margin-left: 1.5rem;
    max-width: 13.8rem;
  }
}
