.footer__text {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0 auto;
  width: 100%;
}

@media screen and (max-width: 1000px) {
  .footer__text {
    max-width: 36.25rem;
  }
}

@media screen and (max-width: 640px) {
  .footer__text {
    max-width: 17.625rem;
    font-size: 14px;
    line-height: 17px;
  }
}
