.profile__info {
  gap: 1rem 1.125rem;
  align-items: center;
  display: grid;
  grid-template-rows: 2.4375rem 1.1875rem;
  grid-template-columns: 2fr 0.1fr;
  grid-auto-rows: auto;
  align-self: center;
}

@media screen and (max-width: 760px) {
  .profile__info {
    margin: 0 0 1.875rem 0;
  }
}

@media screen and (max-width: 420px) {
  .profile__info {
    grid-template-rows: 1.625rem 1.1875rem;
    gap: 0.625rem 0.625rem;
    margin: 0 0 2rem 0;
  }
}
