.popup__close-button {
  position: absolute;
  top: -2.25rem;
  right: -2.5rem;
  width: 2rem;
  height: 2rem;
  background-color: transparent;
  background-image: url(../../../image/close-Icon.svg);
  border: none;
  cursor: pointer;
}

.popup__close-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 540px) {
  .popup__close-button {
    right: 0;
    background-size: contain;
    width: 1.25rem;
    height: 1.25rem;
  }
}
