.profile__add-button {
  border: 2px solid #ffffff;
  width: 9.375rem;
  align-self: center;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.4375rem;
  background: transparent url('../../../image/plus-image.svg') no-repeat center;
  border-radius: 2px;
}

.profile__add-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 760px) {
  .profile__add-button {
    width: 17.625rem;
    padding: 1.4375rem;
    background-size: 16px;
  }
}
