.profile__avatar {
  border-radius: 50%;
  height: 120px;
  width: 120px;
  object-fit: cover;
  object-position: center;
  transition: 0.3s linear;
}

.profile__avatar:hover {
  opacity: 0.2;
  cursor: pointer;
}

@media screen and (max-width: 760px) {
  .profile__avatar {
    width: 7.5rem;
    height: 7.5rem;
    align-self: center;
  }
}
